import gsap from 'gsap'
import ScrollTrigger from 'gsap/ScrollTrigger'

gsap.registerPlugin(ScrollTrigger)

const editionsTitle = document.querySelector('.editions__title')
const editionsTitleWidth = editionsTitle.offsetWidth

window.addEventListener('load', () => {
	gsap.to(editionsTitle, {
		xPercent: -100,
		x: () => innerWidth,
		ease: 'none',
		scrollTrigger: {
			trigger: '.editions__x-scroll',
			start: 'center center',
			end: () => `${editionsTitleWidth - innerWidth}rem center`,
			scrub: true,
			pin: true,
		},
	})
})
